import {Component, Input} from "@angular/core";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {ManagedObject} from "@service/managed-object";
import {VolumeContainerCluster} from "@generated/volume-container-cluster";
import {ClusterFlavor} from "@generated/cluster-flavor";
import {BasicVmData} from "@generated/basic-vm-data";
import {WcpPvcsContext} from "@navigation/context/wcp-pvcs-context";

@Component({
   templateUrl: "volumes-view-details-cluster.html",
   styleUrls: ["volumes-view-details-cluster.scss"],
   selector: "cns-volumes-view-details-cluster",
})

/**
 * The component is used to show cluster details of a CNS volume(e.g., metadata for persistent volumes,
 * persistent volume claims and pods)
 */

export class VolumesViewDetailsCluster {

   constructor(private navigationService: NavigationService) {
   }

   @Input()
   cluster: VolumeContainerCluster;

   @Input()
   volumeName: string;

   @Input()
   vmData: BasicVmData[];

   @Input()
   vmDataLoading: boolean;

   isWCPCluster(): boolean {
      return (this.cluster.flavor === ClusterFlavor.WORKLOAD);
   }

   isGuestCluster(): boolean {
      return (this.cluster.flavor === ClusterFlavor.GUEST);
   }

   navigateToWCPNamespace(namespace: string) {
      this.navigationService.navigateTo(PlatformExtension.LAST_VISITED_PAGE, this.getWCPObjectId(namespace));
   }

   navigateToWCPPvc(namespace: string, pvcName: string) {
      const context: WcpPvcsContext = {
         pvcName: pvcName,
      };
      this.navigationService.navigateTo(
            PlatformExtension.NAMESPACE_STORAGE_PVCS, this.getWCPObjectId(namespace), context);
   }

   private getWCPObjectId(namespace: string): string {
      return `urn:vapi:com.vmware.wcp.WorkloadModel:${namespace}:${ManagedObject.contextObject.serverGuid}`;
   }
}