import {Component, EventEmitter, Input, Output} from "@angular/core";
import {ContainerVolumePerfData} from "@component/file-share-perf/container-volume-perf-data";
import {FileSharePerfData} from "@component/file-share-perf/file-share-perf-data";
import {VolumePerfData} from "@component/file-share-perf/volume-perf-data";
import {UnavailableViewSpec} from "@component/unavailable-view/unavailable-view-spec";
import {VirtualObjectsTypeUtil} from "@component/virtual-objects-table/virtual-objects-type.util";
import {CnsService} from "@generated/cns-service";
import {DatastoreType} from "@generated/datastore-type";
import {DisplayObjectType} from "@generated/display-object-type";
import {LabelData} from "@generated/label-data";
import {PerfMonitorCommonPropsData} from "@generated/perf-monitor-common-props-data";
import {PerfVirtualMachineDiskData} from "@generated/perf-virtual-machine-disk-data";
import {PhysicalPlacementService} from "@generated/physical-placement-service";
import {VirtualObjectModel} from "@generated/virtual-object-model";
import {VirtualObjectPlacementModel} from "@generated/virtual-object-placement-model";
import {Volume} from "@generated/volume";
import {VolumeDetails} from "@generated/volume-details";
import {VsanFileServiceShare} from "@generated/vsan-file-service-share";
import {VsanPerfPropertyProvider} from "@generated/vsan-perf-property-provider";
import {VirtualObjectsContext} from "@navigation/context/virtual-objects-context";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {ErrorHandler} from "@service/flow/error-handler.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {RefreshHandler} from "@service/global-refresh.service";
import {ManagedObject} from "@service/managed-object";
import {MapUtil} from "@util/map-util";
import {RaceConditionSafe} from "@util/promise/race-condition-safe.decorator";
import {VolumesUtil} from "@util/volumes-util";
import {VsanUiUtils} from "@util/vsan-util";
import {NestedPerfEntity} from "../../../performance/model/nested-perf-entity";
import {PerfMultiEntitesDataQueryService} from "../../../performance/util/perf-multi-entities-data-query.service";
import {VolumesVmService} from "../volumes-vm-service";

@Component({
   templateUrl: "volumes-view-details.html",
   styleUrls: ["volumes-view-details.scss"],
   selector: "cns-volumes-view-details",
})

/**
 * The component is used to show details of a CNS volume(e.g., basic properties, attached VMs as well as metadata for
 * persistent volumes, persistent volume claims and pods) and for File volume
 */

export class VolumesViewDetails {

   private static readonly EXTENSION_ID: string = "appplatform.vmware.com/extension-id";

   private _volumeItem: Volume;
   volumeData: VolumeData;

   @Input()
   set volumeItem(value: Volume) {
      this._volumeItem = value;
      this.resetVolumeData();
      if (!this.volumeItem || VsanUiUtils.isEmpty(this.volumeItem.datastoreData)) {
         return;
      }
      this.refresh();
   }

   get volumeItem(): Volume {
      return this._volumeItem;
   }

   @Input()
   storagePolicyName: string;

   // Reset data for the current page (i.e., on reload, page change, filter change)
   @Input()
   set resetPageData(value: any) {
      this.volumeIdToVolumeDetails = new Map();
      this.volumeIdToPhysicalPlacement = new Map();
   }

   @Output()
   onError: EventEmitter<string> = new EventEmitter<string>();

   get unavailableViewSpec(): UnavailableViewSpec {
      if (!VolumesUtil.isAttachedContainerVolume(this.volumeItem)) {
         return UnavailableViewSpec.PERF_NOT_ATTACHED_BLOCK_VOLUME;
      } else if (!VolumesUtil.isAssociatedVmAccessible(this.volumeItem)) {
         return UnavailableViewSpec.PERF_NOT_VM_READ_PERMISSIONS;
      }
   }

   @LoadingIndicator({"name": "vm-data-loader"})
   vmDataLoading: boolean;

   placementDetailsTabActive: boolean;
   performanceTabActive: boolean;
   clusterDetailsTabActive: boolean = true;
   basicsTabActive: boolean;

   private extensionId: string;
   private volumeIdToVolumeDetails: Map<string, VolumeDetails> = new Map();
   private volumeIdToPhysicalPlacement: Map<string, Map<string, VirtualObjectPlacementModel[]>> = new Map();

   constructor(private navigationService: NavigationService,
         private cnsService: CnsService,
         private volumesVmService: VolumesVmService,
         private physicalPlacementService: PhysicalPlacementService,
         private perfProvider: VsanPerfPropertyProvider,
         private perfMultiEntitiesDataQuery: PerfMultiEntitesDataQueryService) {
      this.volumeData = new VolumeData();
   }

   @RefreshHandler
   @Loader()
   async refresh(): Promise<void> {
      this.volumeData = await this.queryAllData(this.volumeItem);
   }

   // Reset data on volume selection change.
   private resetVolumeData(): void {
      this.volumeData.reset();
   }

   isPerformanceApplicable(volumeItem: Volume): boolean {
      return VolumesUtil.isFileVolume(volumeItem) || VolumesUtil.isAssociatedVmAccessible(volumeItem);
   }

   getVirtualObjects(): VirtualObjectModel[] {
      const virtualObjects: any[] = [];
      if (!this.volumeItem || !this.volumeData?.placementData) {
         return virtualObjects;
      }
      // VM Ref is not set as we don't want to show VM path
      this.volumeData.placementData.forEach((placementData, uuid) => {
         virtualObjects.push({
            uid: uuid,
            name: this.volumeItem.name,
            iconId: this.volumeItem.iconId,
            diskUuid: this.isVolumeOnVsanDirectDatastore(this.volumeItem)
                  ? placementData[0].capacityDisk.nodeUuid
                  : null,
         });
      });
      return virtualObjects;
   }

   navigateToVirtualObjects(): void {
      // if there is name, filter by it. Otherwise filter by calculated filter
      const virtualObjectsContext: VirtualObjectsContext = {
         presentVirtualObjectUuids: this.volumeData.volumeDetails.virtualObject.uuids,
         filterTypes: this.getExtensionId()
               ? [
                  VirtualObjectsTypeUtil.getVirtualObjectTypeFilter(DisplayObjectType.EXTENSION_APP,
                        this.getExtensionId()),
               ]
               : [
                  VirtualObjectsTypeUtil.getVirtualObjectTypeFilter(this.volumeData.volumeDetails.virtualObject.filter),
               ],
      };
      this.navigationService.navigateTo(
            PlatformExtension.VIRTUAL_OBJECTS,
            this.volumeData.volumeDetails.cluster,
            virtualObjectsContext);
   }

   async queryVolumeDetails(volume: Volume): Promise<VolumeDetails> {
      if (!this.canGetVirtualObjectsFromDatastore(volume)) {
         return null;
      }
      this.extensionId = this.getExtensionId();

      let volumeDetails: VolumeDetails = this.getVolumeDetails(volume);
      if (volumeDetails) {
         return volumeDetails;
      }

      // Generally, Volume.datastoreData field is an array because:
      // 1) a single NFS folder can be added as a storage to hosts residing in different datacenters.
      // 2) a volume backed by such NFS folder will refer to all such datastores on a VC level.
      // Navigation to virtual objects, however, is available only for a vSAN datastores.
      // Since a volume can be backed by a single vSAN datastore we pass only the first available item.
      volumeDetails = await this.cnsService.getVolumeDetails(
            volume.datastoreData[0].reference,
            volume.id,
            volume.fileshareName,
            !!this.extensionId);
      this.volumeIdToVolumeDetails.set(volume.id, volumeDetails);
      return volumeDetails;

   }

   private getExtensionId(): string {
      return this.getLabelByKey(VolumesViewDetails.EXTENSION_ID);
   }

   private getLabelByKey(labelKey: string): string {
      let labels: LabelData[] = [];
      if (!VsanUiUtils.isEmpty(this.volumeItem.labels)) {
         labels = this.volumeItem.labels.filter(label => label.key === labelKey);
      }
      return (labels.length && labels[0].value)
            ? labels[0].value
            : null;
   }

   @RaceConditionSafe()
   async queryAllData(volume: Volume): Promise<VolumeData> {
      const volumeData: VolumeData = new VolumeData();
      await this.queryVMData(volume);
      volumeData.volumeDetails = await this.queryVolumeDetails(volume);
      [volumeData.placementData, volumeData.performanceData] = await Promise.all([
         this.queryPlacementData(volume, volumeData.volumeDetails),
         this.queryPerformanceData(volume, volumeData.volumeDetails),
      ]);
      return volumeData;
   }

   @Loader({name: "vm-data-loader"})
   private async queryVMData(volume: Volume): Promise<void> {
      try {
         await this.volumesVmService.getVMData(volume);
      } catch (e) {
         this.handleError(e);
      }
   }

   @ErrorHandler()
   private handleError(error: string): void {
      this.onError.emit(error);
   }

   private async queryPlacementData(volumeItem: Volume,
         volumeDetails: VolumeDetails): Promise<Map<string, VirtualObjectPlacementModel[]>> {
      if (!this.canGetVirtualObjectsFromDatastore(volumeItem)) {
         return null;
      }

      // Keep this as any type due to non-REPA code.
      let physicalPlacement: any = this.getPhysicalPlacementData(volumeItem);
      if (physicalPlacement) {
         return physicalPlacement;
      }
      if (this.isVolumeOnVsanDatastore(volumeItem)) {
         physicalPlacement = await this.physicalPlacementService.getVsanPhysicalPlacement(
               volumeDetails.cluster, volumeDetails.virtualObject.uuids);
      } else {
         physicalPlacement = await this.physicalPlacementService.getVsanDirectPhysicalPlacement(
               volumeDetails.cluster, [volumeItem.id]);
      }
      const volumePhysicalPlacement: Map<string, VirtualObjectPlacementModel[]> =
            MapUtil.toMap<string, VirtualObjectPlacementModel[]>(physicalPlacement);
      this.volumeIdToPhysicalPlacement.set(volumeItem.id, volumePhysicalPlacement);
      return volumePhysicalPlacement;
   }

   async fetchDataAfterPerfEnabled(volumeItem: Volume, volumeDetails: VolumeDetails): Promise<void> {
      if (this.volumeData) {
         this.volumeData.performanceData = await this.queryPerformanceData(volumeItem, volumeDetails);
      }
   }

   private async queryPerformanceData(volumeItem: Volume, volumeDetails: VolumeDetails): Promise<VolumePerfData> {
      if (!this.isVolumeOnVsanDatastore(volumeItem) || !this.isPerformanceApplicable(volumeItem)) {
         return null;
      }
      const commonPerfData: PerfMonitorCommonPropsData =
            await this.queryCommonPerfData(volumeDetails.cluster);
      return await this.getVolumePerfData(volumeItem, volumeDetails, commonPerfData);
   }

   private async getVolumePerfData(volumeItem: Volume, volumeDetails: VolumeDetails,
         commonPerfData: PerfMonitorCommonPropsData): Promise<VolumePerfData> {
      if (VolumesUtil.isFileVolume(volumeItem)) {
         return this.getFileVolumePerfData(volumeItem, volumeDetails, commonPerfData);
      } else if (VolumesUtil.isAttachedContainerVolume(volumeItem)) {
         return await this.getBlockVolumePerfData(volumeItem, commonPerfData);
      } else {
         return null;
      }
   }

   private async getBlockVolumePerfData(volumeItem: Volume, commonPerfData: PerfMonitorCommonPropsData):
         Promise<VolumePerfData> {
      const diskData: PerfVirtualMachineDiskData =
            await this.getVirtualMachineDiskData(volumeItem.vmData[0].vmRef);
      const disks: NestedPerfEntity[] = this.perfMultiEntitiesDataQuery.parseVirtualDisk(diskData, false);
      const disk: NestedPerfEntity = disks.find(diskItem => diskItem.entityName.includes(volumeItem.name));
      const containerVolumePerfData: ContainerVolumePerfData = new ContainerVolumePerfData(disk,
            volumeItem.vmData[0].vmRef);
      return new VolumePerfData(commonPerfData, null, containerVolumePerfData);
   }

   private async getVirtualMachineDiskData(vmRef: ManagedObject): Promise<PerfVirtualMachineDiskData> {
      return await this.perfProvider.getVirtualMachineDiskData(vmRef, true);
   }

   private getFileVolumePerfData(volumeItem: Volume, volumeDetails: VolumeDetails,
         commonPerfData: PerfMonitorCommonPropsData): VolumePerfData {
      const fileShare: VsanFileServiceShare = {
         config: {
            name: volumeItem.fileshareName,
            domainName: volumeDetails.fileShare.domainName,
            protocol: volumeDetails.fileShare.protocol,
         },
      } as VsanFileServiceShare;
      // Currently, only NFS file shares provisioned by vSAN file service will be supported.
      // CNS may support non NFS, non vSAN file service based file shares in future.
      const isSmbProtocolSupported: boolean = false;
      const fileSharePerfData: FileSharePerfData = new FileSharePerfData(fileShare, isSmbProtocolSupported);
      return new VolumePerfData(commonPerfData, fileSharePerfData);
   }

   private queryCommonPerfData(cluster: ManagedObject): Promise<PerfMonitorCommonPropsData> {
      return this.perfProvider.getPerfMonitorCommonPropsData(cluster);
   }

   canGetVirtualObjectsFromDatastore(volume: Volume): boolean {
      return this.isVolumeOnVsanDatastore(volume) || this.isVolumeOnVsanDirectDatastore(volume);
   }

   isVolumeOnVsanDatastore(volumeItem: Volume): boolean {
      return volumeItem && volumeItem.datastoreType === DatastoreType.VSAN;
   }

   isVolumeOnVsanDirectDatastore(volumeItem: Volume): boolean {
      return volumeItem && volumeItem.datastoreType === DatastoreType.VSAN_DIRECT;
   }

   private getVolumeDetails(volumeItem: Volume): VolumeDetails {
      return this.volumeIdToVolumeDetails.get(volumeItem.id);
   }

   private getPhysicalPlacementData(volumeItem: Volume): Map<string, VirtualObjectPlacementModel[]> {
      return this.volumeIdToPhysicalPlacement.get(volumeItem.id);
   }
}

class VolumeData {
   volumeDetails: VolumeDetails;
   placementData: Map<string, VirtualObjectPlacementModel[]>;
   performanceData: VolumePerfData;

   reset(): void {
      this.volumeDetails = null;
      this.placementData = null;
      this.performanceData = null;
   }
}
