import {Pipe, PipeTransform} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {CnsHealthStatus} from "@generated/cns-health-status";

/**
 * This pipe returns the correct label for volume health statuses.
 */
@Pipe({
   name: "cnsHealthStatusLabel",
})
export class HealthStatusLabelPipe implements PipeTransform {
   transform(status: CnsHealthStatus): string {
      if (!status) {
         return VsanUiUtils.getString("vsan.na.label");
      }
      switch (status) {
         case CnsHealthStatus.green:
            return VsanUiUtils.getString("cns.volumes.container.health.status.green");
         case CnsHealthStatus.red:
            return VsanUiUtils.getString("cns.volumes.container.health.status.red");
         case CnsHealthStatus.yellow:
            return VsanUiUtils.getString("cns.volumes.container.health.status.yellow");
         case CnsHealthStatus.unknown:
            return VsanUiUtils.getString("cns.volumes.container.health.status.unknown");
      }
   }
}