import {Component, Input} from "@angular/core";
import {BasicVmData} from "@generated/basic-vm-data";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   templateUrl: "cns-vm-cell.html",
   styleUrls: ["cns-vm-cell.scss"],
   selector: "cns-vm-cell",
})

/**
 * The component is used to show VMs, the volume is attached to.
 * When the volume is attached to multiple VMs, a signpost with count is shown next to the
 * first vm (i.e., vm_1_name (+ n)).
 * The component is used in grid details.
 */
export class CnsVmCell {

   @Input()
   vmData: BasicVmData[];

   @Input()
   loading: boolean;

   constructor(private navigationService: NavigationService) {
   }

   navigateToVm(vmRef: ManagedObject): void {
      this.navigationService.navigateTo(PlatformExtension.VM_SUMMARY, vmRef);
   }

   getTooltipText(vmRef: ManagedObject): string {
      return VsanUiUtils.getString("vsan.monitor.cluster.virtualObjects.noReadPermissions", vmRef.value);
   }
}
