import {Component, Input} from "@angular/core";
import {ClusterFlavor} from "@generated/cluster-flavor";
import {VolumeContainerCluster} from "@generated/volume-container-cluster";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {ManagedObject} from "@service/managed-object";

@Component({
   templateUrl: "cns-cluster-cell.html",
   styleUrls: ["cns-cluster-cell.scss"],
   selector: "cns-cluster-cell",
})

/**
 * The component is used to represent volume's clusters. For a vanilla flavor only name is shown. For a WCP/Guest
 * flavor icon and name with link is shown if present, if not only name is shown.
 * If the component is used in details a badge is shown.
 * The component is used in the grid and grid details.
 */
export class CnsClusterCell {

   @Input()
   cluster: VolumeContainerCluster;

   @Input()
   volumeName: string;

   @Input()
   showBadge: boolean;

   constructor(private navigationService: NavigationService) {
   }

   get isVanillaCluster(): boolean {
      return this.isClusterFlavor(ClusterFlavor.VANILLA);
   }

   get isWCPCluster(): boolean {
      return this.isClusterFlavor(ClusterFlavor.WORKLOAD);
   }

   get isGuestCluster(): boolean {
      return this.isClusterFlavor(ClusterFlavor.GUEST);
   }

   private isClusterFlavor(clusterFlavor: ClusterFlavor): boolean {
      return this.cluster && this.cluster.flavor && this.cluster.flavor === clusterFlavor;
   }

   navigateToSupervisorCluster(clusterRef: ManagedObject): void {
      this.navigationService.navigateTo(PlatformExtension.CLUSTER_CNS_VOLUMES, clusterRef);
   }

   navigateToGuestCluster(resPoolRef: ManagedObject): void {
      this.navigationService.navigateTo(PlatformExtension.LAST_VISITED_PAGE, resPoolRef);
   }
}