import {Component, Input} from "@angular/core";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {Volume} from "@generated/volume";
import {VsanUiUtils} from "@util/vsan-util";
import {CnsVolumeType} from "@generated/cns-volume-type";
import {SharesContext} from "@navigation/context/shares-context";
import {FileShareTypeFilterOptions} from "../../../file-services/shares/filters/file-share-type-filter-options";
import {BasicVmData} from "@generated/basic-vm-data";
import {VolumesUtil} from "@util/volumes-util";

@Component({
   templateUrl: "volumes-view-details-basics.html",
   styleUrls: ["volumes-view-details-basics.scss"],
   selector: "cns-volumes-view-details-basics",
})

/**
 * The component is used to show basic details of a CNS volume(e.g., name, storage policy, datastore, etc.)
 */

export class VolumesViewDetailsBasics {

   VolumesUtil: typeof VolumesUtil = VolumesUtil;

   constructor(private navigationService: NavigationService) {
   }

   @Input()
   volumeItem: Volume;

   @Input()
   storagePolicyName: string;

   @Input()
   vmData: BasicVmData[];

   @Input()
   vmDataLoading: boolean;

   getVMLabel(): string {
      return VsanUiUtils.getString((!VsanUiUtils.isEmpty(this.vmData) &&
            this.vmData[0].isPodVM) ?
            "cns.volumes.container.grid.details.tab.basics.column.pod" :
            "cns.volumes.container.grid.details.tab.basics.column.vm");
   }

   navigateToFileShares() {
      const fileServiceSharesContext: SharesContext = {
         preselectedShareName: this.volumeItem.fileshareName,
         shareTypeFilter: FileShareTypeFilterOptions.CNS_VOLUME,
      };
      this.navigationService.navigateTo(PlatformExtension.FILE_SERVICE_SHARES, null, fileServiceSharesContext);
   }
}