import {Injectable} from "@angular/core";
import {CnsVolumeType} from "@generated/cns-volume-type";
import {BasicVmData} from "@generated/basic-vm-data";
import {Volume} from "@generated/volume";
import {CnsService} from "@generated/cns-service";
/**
 * A service that checks whether a block volume is attached to a VM.
 */
@Injectable()
export class VolumesVmService  {
   constructor(private cnsService: CnsService) {
   }

   async getVMData(volumeItem: Volume): Promise<void> {
      // Don't fetch vm data for non-block volumes or if it is already fetched.
      if (volumeItem.type !== CnsVolumeType.BLOCK || volumeItem.vmData) {
         return;
      }
      // It is not necessary to call the method for each datastore (e.g., in case of having added
      // multiple NFS datastores to separate DCs) because the API already returns all the VM data.
      const vmData: BasicVmData[] = await this.cnsService.getVolumeVmsData(
            volumeItem.datastoreData[0].reference, volumeItem.id);
      volumeItem.vmData = vmData;
   }
}
