import {ChangeDetectorRef, Component, OnDestroy, OnInit, ViewChild} from "@angular/core";
import {ClrDatagrid, ClrDatagridPagination, ClrDatagridStateInterface} from "@clr/angular";
import {LabelCellService} from "@component/cell/label-cell.service";
import {FilterItem} from "@component/cross-column-filter/filter-item";
import {PaneType} from "@component/master-detail/slide-panel.component";
import {CnsHealthStatus} from "@generated/cns-health-status";
import {CnsService} from "@generated/cns-service";
import {PbmDataProvider} from "@generated/pbm-data-provider";
import {PermissionService} from "@generated/permission-service";
import {Privileges} from "@generated/privileges";
import {StorageCompliance} from "@generated/storage-compliance";
import {Volume} from "@generated/volume";
import {VolumeContainerCluster} from "@generated/volume-container-cluster";
import {VolumeFilterSpec} from "@generated/volume-filter-spec";
import {VsanCapabilityProvider} from "@generated/vsan-capability-provider";
import {VolumesContext} from "@navigation/context/volumes-context";
import {RefreshHandler} from "@service/global-refresh.service";
import {ManagedObject, ManagedObjectUtils} from "@service/managed-object";
import {ScreenReaderAnnouncer} from "@service/screen-reader-announcer.service";
import {Icon} from "@util/icon";
import {Logger} from "@util/logger";
import {ModalBuilder} from "@util/modal-builder";
import {VsanUiUtils} from "@util/vsan-util";
import {Subject, Subscription} from "rxjs";

@Component({
   templateUrl: "volumes.html",
   styleUrls: ["volumes.scss"],
   selector: "cns-volumes",
   providers: [LabelCellService],
})
export class CnsVolumesComponent implements OnInit, OnDestroy {
   readonly CnsVolumeField: typeof CnsVolumeField = CnsVolumeField;
   readonly DEFAULT_ITEMS_PER_PAGE: number = 20;
   private static readonly DEFAULT_START_INDEX: number = 0;
   private static readonly NO_STORAGE_POLICY_VALUE: string = "";
   private static readonly FIRST_PAGE_CLASS: string = ".pagination-first";

   Icon: typeof Icon = Icon;
   StorageCompliance: typeof StorageCompliance = StorageCompliance;
   CnsHealthStatus: typeof CnsHealthStatus = CnsHealthStatus;
   enumToArray: (data: any) => any[] = VsanUiUtils.enumToArray;
   VsanUiUtils: typeof VsanUiUtils = VsanUiUtils;
   // Pagination & items data
   volumes: Volume[];
   total: number;
   loading: boolean = true;
   errorMessages: string[] = [];
   // Filters
   filters: FilterItem[];

   hasReadPolicyPermission: boolean = false;
   hasUpdatePolicyPermission: boolean = false;
   hasDatastoreFileManagementPermission: boolean = false;
   hasHostStoragePermission: boolean = false;
   isDatastoreContext: boolean;
   storagePolicies: any;
   selectedVolumes: Volume[] = [];
   isFileVolumesSupported: boolean = false;
   isVolumeRelocationSupported: boolean = false;
   isCnsReconfigPolicySupported: boolean = false;
   volumeDetailsResetData: any = {};

   // Others
   toggleFilterSubject: Subject<FilterItem> = new Subject<FilterItem>();
   refreshSubject: Subject<ClrDatagridStateInterface> = new Subject<ClrDatagridStateInterface>();
   private refreshSubscription: Subscription;
   private labelClickSubscription: Subscription;
   private state: ClrDatagridStateInterface = {};
   private gridFirstRefresh: boolean = true;

   @ViewChild("dataGrid", {static: true})
   dataGrid: ClrDatagrid;

   @ScreenReaderAnnouncer()
   srOnlyMessage: string;

   constructor(private cnsService: CnsService,
         private pbmProvider: PbmDataProvider,
         private permissionService: PermissionService,
         private changeDetector: ChangeDetectorRef,
         private capabilityProvider: VsanCapabilityProvider,
         private labelCellService: LabelCellService,
   ) {
   }

   ngOnInit(): void {
      this.preselectFilter();
      this.refreshSubscription = this.refreshSubject
            .subscribe(async (state: ClrDatagridStateInterface) => {
               this.loading = true;
               this.errorMessages = [];
               await this.dispatchFilterRequestPromise(state);
               this.ensureGridSelectionCheckboxes();
               this.loading = false;
            });
      this.labelClickSubscription = this.labelCellService.onLabelClicked.subscribe((label) => {
         this.toggleFilterSubject.next(new FilterItem(CnsVolumeField.COLUMN_LABELS, [label]));
      });
      this.isDatastoreContext = ManagedObjectUtils.isDatastore(ManagedObject.contextObjectUid);
   }

   // Handle navigation from Virtual Objects and WCP by preselecting the passed filter.
   // The filter key comprises of the "volume" prefix attached to the grid field (i.e., CnsVolumeField enum)
   // in Pascal case (e.g., volume + PvName = volumePvName).
   private preselectFilter(): void {
      const context: VolumesContext = VsanUiUtils.getNavigationData();
      if (!context) {
         return;
      }
      const filters: FilterItem[] = [];
      Object.values(CnsVolumeField).forEach((gridFieldName: string) => {
         const contextFieldValue: any = context[this.buildVolumeKey(gridFieldName)];
         if (contextFieldValue) {
            filters.push(new FilterItem(gridFieldName, contextFieldValue));
         }
      });
      if (!VsanUiUtils.isEmpty(filters)) {
         this.filters = filters;
      }
   }

   private buildVolumeKey(gridFieldName: string): string {
      const volumeContextFieldPrefix: string = "volume";
      return volumeContextFieldPrefix + gridFieldName.charAt(0).toUpperCase() + gridFieldName.slice(1);
   }

   ngOnDestroy(): void {
      this.refreshSubscription.unsubscribe();
      this.labelClickSubscription.unsubscribe();
   }

   filterChange(filters: FilterItem[]): void {
      this.filters = filters;
      // If total is changed to a smaller value when the current page is > 1, the clrDgRefresh event is fired which
      // leads to an additional request.
      // The following code solves that and ensures the current page is 1 when applying filter.
      if (this.state && this.state.page && this.state.page.from &&
            this.state.page.from > CnsVolumesComponent.DEFAULT_START_INDEX) {
         $(CnsVolumesComponent.FIRST_PAGE_CLASS).click();
      } else {
         this.refreshSubject.next(this.state);
      }
   }

   @RefreshHandler
   refresh(): void {
      this.loading = true;
      this.errorMessages = [];
      // Fetch storage policies on initial page load and refresh button click only.
      const storagePoliciesPromises: Promise<any>[] = this.loadStoragePolicies();
      const capabilityPromises: Promise<boolean | void>[] = this.loadCapabilities();
      const volumesPromise: Promise<void> = this.dispatchFilterRequestPromise(this.state);
      Promise.all([...storagePoliciesPromises, volumesPromise, ...capabilityPromises]).then(() => {
         this.loading = false;
         this.ensureGridSelectionCheckboxes();
         // Make sure the labels are correctly expanded, because for some reason they are not validated
         // after the label column is resized on init, which causes the labels to be not correctly expanded.
         setTimeout(() => this.changeDetector.detectChanges());
         this.srOnlyMessage = VsanUiUtils.getString("vsan.screenReader.message.page.loaded",
               VsanUiUtils.getString("cns.monitor.volumes"));
      });
   }

   private ensureGridSelectionCheckboxes(): void {
      // Remove datagrid selection checkboxes when the user has no permissions
      if (!this.hasReadUpdatePolicyPermission() &&
            !this.hasHostStoragePermission &&
            !this.hasDatastoreFileManagementPermission) {
         // this.selectedVolumes = null; - this takes effect as well
         this.dataGrid.selection.selectionType = this.dataGrid.SELECTION_TYPE.None;
      }
   }

   hasReadUpdatePolicyPermission(): boolean {
      return this.hasReadPolicyPermission && this.hasUpdatePolicyPermission;
   }

   private loadStoragePolicies(): Promise<any>[] {
      const promises: Promise<any>[] = [];
      promises.push(this.permissionService.hasVcPermissions(ManagedObject.contextObject, [Privileges.READ_POLICIES()])
            .then(hasReadPolicyPermission => this.hasReadPolicyPermission = hasReadPolicyPermission)
            .catch(e => this.onError(e)));
      promises.push(this.permissionService.hasVcPermissions(ManagedObject.contextObject, [Privileges.UPDATE_POLICIES()])
            .then(hasUpdatePolicyPermission => this.hasUpdatePolicyPermission = hasUpdatePolicyPermission)
            .catch(e => this.onError(e)));
      promises.push(this.permissionService.queryPermissions(ManagedObject.contextObject,
            [Privileges.CONFIG_STORAGE(), Privileges.DATASTORE_FILE_MANAGEMENT()])
            .then(result => {
               this.hasHostStoragePermission = result[Privileges.CONFIG_STORAGE()];
               this.hasDatastoreFileManagementPermission = result[Privileges.DATASTORE_FILE_MANAGEMENT()];
            })
            .catch(e => this.onError(e)));
      promises.push(this.pbmProvider.getStoragePolicyIdNameMap(ManagedObject.contextObject)
            .then(storagePolicies => this.storagePolicies = storagePolicies)
            .catch(e => this.onError(e)));
      return promises;
   }

   private loadCapabilities(): Promise<boolean | void>[] {
      const promises: Promise<any>[] = [];
      // TODO: (tnojarova) confirm whether host capability should be checked as well
      promises.push(this.capabilityProvider.getIsVolumeRelocationSupportedOnVc(ManagedObject.contextObject)
            .then(isVolumeRelocationSupported => {
               this.isVolumeRelocationSupported = isVolumeRelocationSupported;
            })
            .catch(e => Logger.error(e)));
      // TODO: (vasilevd) Move the check to host level
      promises.push(this.capabilityProvider.getIsCnsReconfigPolicySupportedOnVc(ManagedObject.contextObject)
            .then(isCnsReconfigPolicySupported => {
               this.isCnsReconfigPolicySupported = isCnsReconfigPolicySupported;
            })
            .catch(e => Logger.error(e)));
      promises.push(this.capabilityProvider.getIsFileVolumesSupportedOnVc(ManagedObject.contextObject)
            .then(isFileVolumesSupported => {
               this.isFileVolumesSupported = isFileVolumesSupported;
               this.changeDetector.detectChanges();
            })
            .catch(e => {
               this.isFileVolumesSupported = false;
               Logger.error(e);
            }));

      return promises;
   }

   datagridRefresh(state: ClrDatagridStateInterface): void {
      // Remember the last state to use it again when the global refresh button is clicked
      this.state = state;
      // clrDgRefresh event is fired on initial load and each filtering/paging operation.
      // It's not needed to fetch data on initial grid load because this is done when global refresh handler is attached
      if (this.gridFirstRefresh) {
         this.gridFirstRefresh = false;
         this.refresh();
      } else {
         this.loading = true;
         this.refreshSubject.next(state);
      }
   }

   dispatchFilterRequestPromise = (state: ClrDatagridStateInterface): Promise<void> => {
      this.selectedVolumes = [];
      this.volumeDetailsResetData = {};
      const requestFilters: VolumeFilterSpec = this.createRequestFilters(state);
      if (requestFilters) {
         return this.loadVolumes(requestFilters);
      } else {
         this.volumes = [];
         this.total = 0;
         return null;
      }
   };

   private loadVolumes(requestFilters: VolumeFilterSpec): Promise<void> {
      return this.cnsService.getVolumes(ManagedObject.contextObject, requestFilters).then(volumesResult => {
         this.volumes = volumesResult.volumes;
         this.total = volumesResult.total;
      }).catch(e => this.onError(e));
   }

   onError(e: string): void {
      Logger.error(e);
      this.loading = false;
      this.errorMessages.push(e);
   }

   private createRequestFilters(state: ClrDatagridStateInterface): VolumeFilterSpec {
      let fetchVolumes: boolean = true;
      const requestFilters: VolumeFilterSpec = {} as VolumeFilterSpec;

      if (this.filters) {
         for (const filter of this.filters) {
            if (filter.field === CnsVolumeField.COLUMN_STORAGE_POLICY_NAME) {
               if (filter.value === VsanUiUtils.getString("vsan.na.label")) {
                  requestFilters[filter.field] = CnsVolumesComponent.NO_STORAGE_POLICY_VALUE;
               } else {
                  const storagePolicyIdsFilter: string[] = Object.keys(this.storagePolicies).filter(
                        policyId => this.storagePolicies[policyId].toLowerCase()
                              .indexOf(filter.value.toString().toLowerCase()) > -1);
                  // Backend filtration works for a single policy ID.
                  // If no policy ID is found or there are multiple policy IDs update filter labels
                  // and do not return any result.
                  // TODO: change that behavior once backend starts to support filtration by multiple policy IDs
                  if (!VsanUiUtils.isEmpty(storagePolicyIdsFilter) && storagePolicyIdsFilter.length === 1) {
                     requestFilters[filter.field] = storagePolicyIdsFilter[0];
                  } else {
                     fetchVolumes = false;
                  }
               }
            } else {
               requestFilters[filter.field] = filter.value;
            }
         }
         if (!fetchVolumes) {
            return null;
         }
      }
      if (state.page) {
         requestFilters.offset = state.page.from;
         requestFilters.limit = state.page.size;
      } else {
         requestFilters.offset = CnsVolumesComponent.DEFAULT_START_INDEX;
         requestFilters.limit = this.DEFAULT_ITEMS_PER_PAGE;
      }
      return requestFilters;
   }

   storagePolicyNameFromId(policyId: string): string {
      if (policyId) {
         // If a volume has a policy that has been deleted return the policy ID as a name.
         return (this.storagePolicies && this.storagePolicies[policyId])
               ? this.storagePolicies[policyId]
               : policyId;
      } else {
         return null;
      }
   }

   /**
    * Returns all volume clusters including TKCs.
    * @param volume
    */
   getVolumeClusters(volume: Volume): VolumeContainerCluster[] {
      if (!volume || VsanUiUtils.isEmpty(volume.containerClusters)) {
         return [];
      }
      return [].concat(volume.guestClusters, volume.containerClusters);
   }

   openWhatIsThisDialog(): void {
      new ModalBuilder("cnsVolumes/whatIsThis")
            .setDimensions(ModalBuilder.WIZARD_XL)
            .open();
   }

   // SlidePanelComponent specifics:
   isLeftVisible: boolean = true;
   selectedItem: Volume;

   get activePane(): PaneType {
      return this.isLeftVisible
            ? PaneType.LEFT
            : PaneType.RIGHT;
   }

   set activePane(pane: PaneType) {
      this.isLeftVisible = pane === PaneType.LEFT;
   }

   selectTarget(item: Volume): void {
      this.selectedItem = item;
   }

   isSelected(item: Volume): boolean {
      return this.selectedItem === item && !this.isLeftVisible;
   }

   toggle(item: any): void {
      if (this.selectedItem === item) {
         this.isLeftVisible = !this.isLeftVisible;
      } else {
         this.selectTarget(item);
         if (this.isLeftVisible) {
            this.isLeftVisible = !this.isLeftVisible;
         }
      }
   }

   getPaginationLabel(pagination: ClrDatagridPagination): string {
      return VsanUiUtils.getDatagridPaginatedFooterLabel(pagination.totalItems, pagination.firstItem,
            pagination.lastItem, this.DEFAULT_ITEMS_PER_PAGE,
            "cns.volumes.container.grid.volume",
            "cns.volumes.container.grid.volumes",
            "cns.volumes.container.grid.page");
   }
}

export enum CnsVolumeField {
   COLUMN_ID = "id",
   COLUMN_NAME = "name",
   COLUMN_LABELS = "labels",
   COLUMN_DATASTORE = "datastore",
   COLUMN_CONTAINER_CLUSTER_ID = "containerCluster",
   COLUMN_STORAGE_POLICY_NAME = "storagePolicy",
   COLUMN_HEALTH_STATUS = "healthStatus",
   COLUMN_COMPLIANCE_STATUS = "complianceStatus",
   COLUMN_NAMESPACE = "namespace",
   COLUMN_PV_NAME = "pvName",
   COLUMN_PVC_NAME = "pvcName",
   COLUMN_POD_NAME = "podName",
}
