import {Component, OnInit} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {Icon} from "@util/icon";
import {Logger} from "@util/logger";
import {CnsService} from "@generated/cns-service";
import {CnsHostData} from "@generated/cns-host-data";
import {Volume} from "@generated/volume";
import {CnsHealthStatus} from "@generated/cns-health-status";
import {CnsDatastoreAccessibilityStatus} from "@generated/cns-datastore-accessibility-status";

/**
 * Dialog which represents the hosts and their accessibility to datastores.
 */
@Component({
   selector: "cns-volume-accessibility-dialog",
   templateUrl: "volume-accessibility-dialog.html",
})

export class VolumeAccessibilityDialogComponent implements OnInit {
   volume: Volume;
   busy: boolean;
   alertMessages: string[] = [];
   hosts: CnsHostData[] = [];
   Icon = Icon;

   constructor(private cnsService: CnsService) {
   }

   ngOnInit() {
      this.busy = true;
      this.volume = VsanUiUtils.getModalContext().volume;

      const datastoreRefs: ManagedObject[] = this.volume.datastoreData.map(data => data.reference);
      this.cnsService.getHostsDataByDatastoreRefs(datastoreRefs)
            .then((result: CnsHostData[]) => {
               this.hosts = result;
               this.busy = false;
            }).catch(error => {
         Logger.error(error);
         this.alertMessages = [error];
         this.busy = false;
      });
   }

   getHealthStatus(accessibility: CnsDatastoreAccessibilityStatus): CnsHealthStatus {
      switch (accessibility) {
         case CnsDatastoreAccessibilityStatus.accessible:
            return CnsHealthStatus.green;
         case CnsDatastoreAccessibilityStatus.notAccessible:
            return CnsHealthStatus.red;
      }
   }
}