import {Component, Input} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {NavigationService, PlatformExtension} from "@navigation/navigation.service";
import {Icon} from "@util/icon";
import {VolumeDatastoreData} from "@generated/volume-datastore-data";

@Component({
   templateUrl: "cns-datastore-cell.html",
   styleUrls: ["cns-datastore-cell.scss"],
   selector: "cns-datastore-cell",
})

/**
 * The component is used to show the datastore, the volume is backed by. When NFS datastore is used and it is mounted
 * to multiple hosts within different DCs a signpost with count is shown next to the
 * first datastore (i.e., datastore_1_name (+ n))
 * The component is used in the grid and grid details.
 */
export class CnsDatastoreCell {

   @Input()
   datastoreData: VolumeDatastoreData[];

   Icon = Icon;

   constructor(private navigationService: NavigationService) {
   }

   navigateToDatastore(datastore: ManagedObject) {
      this.navigationService.navigateTo(PlatformExtension.DATASTORE_CNS_VOLUMES, datastore);
   }
}
