import {Injectable} from "@angular/core";
import {DatastoreType} from "@generated/datastore-type";
import {StorageCompliance} from "@generated/storage-compliance";
import {Volume} from "@generated/volume";
import {ActionAvailableStatus} from "@util/validation/action-available-status";
import {Validator} from "@util/validation/validator";
import {ValidatorsGroup} from "@util/validation/validators-group";
import {VolumesUtil} from "@util/volumes-util";
import {VsanUiUtils} from "@util/vsan-util";
import {VolumeActionContext} from "./volume-action-context";

@Injectable()
export class VolumeActionAvailabilityService {
   // Volume action IDs
   static readonly VOLUME_DELETE_ID: string = "delete-volume";
   static readonly VOLUME_MIGRATE_ID: string = "migrate-volume";
   static readonly VOLUME_REAPPLY_POLICY_ID: string = "reapply-policy-volume";

   getReapplyPolicyEligibleVolumes(isCnsReconfigPolicySupported: boolean, volumes: Volume[]): Volume[] {
      return volumes.filter(v => v.storagePolicyId &&
            (v.compliance === StorageCompliance.outOfDate ||
                  this.isNonCompliantPolicyReapplyEligible(isCnsReconfigPolicySupported, v)));
   }

   private isNonCompliantPolicyReapplyEligible(isCnsReconfigPolicySupported: boolean, volume: Volume): boolean {
      return isCnsReconfigPolicySupported
            && (volume.compliance === StorageCompliance.nonCompliant)
            && [DatastoreType.VSAN_DIRECT.toString(), DatastoreType.VMFS.toString()].includes(
                  volume.datastoreType);
   }

   checkAvailability = (context: VolumeActionContext): ActionAvailableStatus => {
      switch (context.actionId) {
         case VolumeActionAvailabilityService.VOLUME_REAPPLY_POLICY_ID:
            return this.validate(this.reapplyPolicyVolumeAvailability, context,
                  "cns.volumes.container.reapply.description");
         case VolumeActionAvailabilityService.VOLUME_DELETE_ID:
            return this.validate(this.deleteVolumeAvailability, context,
                  "cns.volumes.container.delete.description");
         case VolumeActionAvailabilityService.VOLUME_MIGRATE_ID:
            return this.validate(this.migrateVolumeAvailability, context,
                  "cns.volumes.container.migrate.description");
      }
   };

   private validate(validatorsGroup: ValidatorsGroup<VolumeActionContext>,
         context: VolumeActionContext,
         successMessageKey: string): ActionAvailableStatus {

      const errorMessage: string = validatorsGroup.getFirstError(context);
      const isActionAvailable: boolean = VsanUiUtils.isEmpty(errorMessage);
      const statusMessage: string = isActionAvailable
            ? VsanUiUtils.getString(successMessageKey)
            : errorMessage;

      return new ActionAvailableStatus(isActionAvailable, statusMessage);
   }

   private readonly hasSelectedVolumes: Validator<VolumeActionContext> = new Validator(
         (context: VolumeActionContext): boolean =>
               VsanUiUtils.isEmpty(context.selectedVolumes),
         VsanUiUtils.getString("cns.volumes.container.action.disabled.noSelection"));

   private readonly singleVolumeSelection: Validator<VolumeActionContext> = new Validator(
         (context: VolumeActionContext): boolean =>
               context.selectedVolumes?.length > 1,
         VsanUiUtils.getString("cns.volumes.container.action.disabled.multipleSelection"));

   private readonly isBlockVolume: Validator<VolumeActionContext> = new Validator(
         (context: VolumeActionContext): boolean =>
               !VolumesUtil.isBlockVolume(context.selectedVolumes[0]),
         VsanUiUtils.getString("cns.volumes.container.action.disabled.fileVolumeSelection"));

   private readonly policiesAreUpToDate: Validator<VolumeActionContext> = new Validator(
         (context: VolumeActionContext): boolean =>
               VsanUiUtils.isEmpty(this.getReapplyPolicyEligibleVolumes(
                     context.isCnsReconfigPolicySupported, context.selectedVolumes)),
         (context: VolumeActionContext): string => this.getReapplyPolicyValidationMessage(
               context.isCnsReconfigPolicySupported));

   private getReapplyPolicyValidationMessage(isCnsReconfigPolicySupported: boolean): string {
      const key: string = isCnsReconfigPolicySupported
            ? "cns.volumes.container.reapply.disabled.description.new"
            : "cns.volumes.container.reapply.disabled.description";
      return VsanUiUtils.getString(key);
   }

   private readonly hasDeletePermissions: Validator<VolumeActionContext> = new Validator(
         (context: VolumeActionContext): boolean =>
               (VolumesUtil.isBlockVolume(context.selectedVolumes[0]) && !context.hasDatastoreFileManagementPermission)
               || (VolumesUtil.isFileVolume(context.selectedVolumes[0]) && !context.hasHostStoragePermission),
         VsanUiUtils.getString("vsan.common.nopermissions"));

   // TODO tnojarova: add a permission check if there is an attached VM - Resource.ColdMigrate
   private readonly hasMigratePermissions: Validator<VolumeActionContext> = new Validator(
         (context: VolumeActionContext): boolean =>
               !context.hasReadPolicyPermission || !context.hasDatastoreFileManagementPermission,
         VsanUiUtils.getString("vsan.common.nopermissions"));

   private readonly reapplyPolicyVolumeAvailability: ValidatorsGroup<VolumeActionContext> = new ValidatorsGroup([
      this.hasSelectedVolumes,
      this.policiesAreUpToDate,
   ]);

   private readonly deleteVolumeAvailability: ValidatorsGroup<VolumeActionContext> = new ValidatorsGroup([
      this.hasSelectedVolumes,
      this.singleVolumeSelection,
      this.hasDeletePermissions,
   ]);

   private readonly migrateVolumeAvailability: ValidatorsGroup<VolumeActionContext> = new ValidatorsGroup([
      this.hasSelectedVolumes,
      this.singleVolumeSelection,
      this.isBlockVolume,
      this.hasMigratePermissions,
   ]);
}