import {
   AfterViewInit,
   ChangeDetectorRef,
   ComponentFactory,
   ComponentFactoryResolver,
   ComponentRef,
   Directive,
   HostListener,
   Input, Renderer2, ViewContainerRef,
} from "@angular/core";
import {ClipboardIcon} from "./clipboard-icon.component";

/**
 * This directive does visually the same thing as vsan-show-title but handles a scenario when the parent's content
 * must be wrapped and at the same time some of it's children overflow parent's max width.
 * vsan-show-title directive cannot be used because the parent's element cannot be set with white-space: nowrap style
 * and if that style is applied to the element itself it make no sense as it's content is overflowed by the parent
 * element. To handle that the new directive positions absolutely an element that represents the ellipsis as well as
 * the copyToClipboard icon.
 */
@Directive({
   selector: "[vsan-show-title-multi-line]",
})
export class ShowTitleMultiLineDirective implements AfterViewInit {

   @Input()
   copyToClipboard: boolean;

   private static readonly ELLIPSIS_STRING: string = "...";
   private static readonly ELLIPSIS_CLASS_NAME: string = "custom-ellipsis";
   private static readonly CLIPBOARDICON_SPACING: string = "0.6rem";
   private static readonly ELLIPSIS_SPACING: string = "0rem";

   private clipboardIcon: ComponentRef<ClipboardIcon>;
   private originalParentPosition: string;
   private ellipsisSpan: HTMLSpanElement;

   constructor(
         private viewContainerRef: ViewContainerRef,
         private resolver: ComponentFactoryResolver,
         private renderer: Renderer2,
         private cdr: ChangeDetectorRef) {
   }

   ngAfterViewInit() {
      if (this.isTextEllipsisActive()) {
         this.originalParentPosition = this.element.parentElement.style.position;
         this.element.parentElement.style.position = "relative";
         this.addEllipsis();
      }
   }

   ngOnDestroy(): void {
      if (this.isElementCreated()) {
         this.element.parentElement.style.position = this.originalParentPosition;
      }
   }

   @HostListener("mouseenter")
   show(): void {
      if (!this.isTextEllipsisActive()) {
         return;
      }
      this.setTitle();
      this.addClipboardIcon();
   }

   @HostListener("mouseleave")
   hide(): void {
      if (!this.isElementCreated()) {
         return;
      }
      this.removeClipboardIcon();
      this.removeTitle();
      this.cdr.detectChanges();
   }

   private get element(): HTMLElement {
      return this.viewContainerRef.element.nativeElement;
   }

   private isElementCreated(): boolean {
      return this.viewContainerRef && this.viewContainerRef.element && this.viewContainerRef.element.nativeElement;
   }

   private isTextEllipsisActive(): boolean {
      return this.getElRect().right > this.getParentRect().right;
   }

   private getElRect(): ClientRect | DOMRect {
      return this.element.getBoundingClientRect();
   }

   private getParentRect(): ClientRect | DOMRect {
      return this.element.parentElement.getBoundingClientRect();
   }

   private setTitle(): void {
      this.element.setAttribute("title", this.innerTextStripped);
   }

   private removeTitle(): void {
      this.element.removeAttribute("title");
   }

   private addClipboardIcon(): void {
      if (!this.copyToClipboard) {
         return;
      }
      this.createClipboardIcon(this.innerTextStripped);
      this.ellipsisSpan.style.right = ShowTitleMultiLineDirective.CLIPBOARDICON_SPACING;
   }

   private get innerTextStripped(): string {
      const elementText: string = this.element.innerText;
      const ellipsisIndex: number = elementText.lastIndexOf(ShowTitleMultiLineDirective.ELLIPSIS_STRING);
      return elementText.substring(0, ellipsisIndex);
   }

   private createClipboardIcon(copyContent): void {
      const factory: ComponentFactory<ClipboardIcon> = this.resolver.resolveComponentFactory(ClipboardIcon);
      this.clipboardIcon = this.viewContainerRef.createComponent(factory);
      const clipboardIconEl: HTMLElement = this.clipboardIcon.injector.get(ClipboardIcon).element.nativeElement;
      this.setClipBoardIconTop(clipboardIconEl);
      this.renderer.appendChild(this.viewContainerRef.element.nativeElement, clipboardIconEl);
      this.clipboardIcon.instance.content = copyContent;
   }

   private addEllipsis() {
      this.ellipsisSpan = document.createElement("span");
      this.ellipsisSpan.className += ShowTitleMultiLineDirective.ELLIPSIS_CLASS_NAME;
      this.ellipsisSpan.innerText = ShowTitleMultiLineDirective.ELLIPSIS_STRING;
      this.ellipsisSpan.style.top = this.topPostion;
      this.setEllipsisInitialPadding();
      this.renderer.appendChild(this.viewContainerRef.element.nativeElement, this.ellipsisSpan);
   }

   private setClipBoardIconTop(clipboardIconEl: HTMLElement) {
      clipboardIconEl.style.top = this.topPostion;
   }

   private get topPostion(): string {
      const top: number = (this.getElRect().top - this.getParentRect().top);
      return `${top}px`;
   }

   private removeClipboardIcon(): void {
      if (!this.clipboardIcon) {
         return;
      }
      this.setEllipsisInitialPadding();
      this.clipboardIcon.destroy();
   }

   private setEllipsisInitialPadding(): void {
      this.ellipsisSpan.style.right = ShowTitleMultiLineDirective.ELLIPSIS_SPACING;
   }
}