import {Component, OnInit} from "@angular/core";
import {CnsService} from "@generated/cns-service";
import {VolumeComplianceFailure} from "@generated/volume-compliance-failure";
import {ErrorMessage} from "@service/flow/error-message.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   selector: "cns-compliance-failures-dialog",
   templateUrl: "compliance-failures-dialog.html",
})
export class ComplianceFailuresDialogComponent implements OnInit {
   volumeName: string;
   failures: VolumeComplianceFailure[] = [];
   @LoadingIndicator()
   busy: boolean;
   @ErrorMessage()
   alertMessage: string;

   constructor(private cnsService: CnsService) {
   }

   @Loader()
   async ngOnInit(): Promise<void> {
      const modalContext: any = VsanUiUtils.getModalContext();
      this.volumeName = modalContext.volume.name;
      this.failures = await this.cnsService.loadComplianceFailures(ManagedObject.contextObject, modalContext.volume);
   }
}