import {Component, EventEmitter, Input, Output} from "@angular/core";
import {Volume} from "@generated/volume";
import {ManagedObject} from "@service/managed-object";
import {TaskMonitorService} from "@service/task-monitor-service";
import {ModalBuilder} from "@util/modal-builder";
import {ActionAvailableStatus} from "@util/validation/action-available-status";
import {VsanDialogUtil} from "@util/vsan-dialog-util";
import {VsanRoutes} from "../../../../vsan-routes";
import {CnsVolumesDeleteVolumeModalContext} from "./delete-volume/delete-volume-modal-context.data";
import {CnsVolumesMigrateVolumeModalContext} from "./migrate-volume/migrate-volume-modal-context.data";
import {ReapplyPolicyVolumeModalContextData} from "./reapply-policy-volume/reapply-policy-volume-modal-context.data";
import {VolumeActionAvailabilityService} from "./volume-action-availability.service";
import {VolumeActionContext} from "./volume-action-context";

@Component({
   templateUrl: "volume-actions.html",
   selector: "cns-volume-actions",
   styleUrls: ["volume-actions.scss"],
})
export class CnsVolumeActionsComponent {
   @Input()
   selectedVolumes: Volume[] = [];

   @Input()
   storagePolicyName: string;

   @Input()
   isVolumeRelocationSupported: boolean;

   @Input()
   isCnsReconfigPolicySupported: boolean;

   @Input()
   loading: boolean;

   @Input()
   hasReadPolicyPermission: boolean;

   @Input()
   hasUpdatePolicyPermission: boolean;

   @Input()
   hasDatastoreFileManagementPermission: boolean;

   @Input()
   hasHostStoragePermission: boolean;

   @Output()
   onError: EventEmitter<string> = new EventEmitter<string>();

   @Output()
   onSuccess: EventEmitter<void> = new EventEmitter<void>();

   VolumeActionAvailabilityService: typeof VolumeActionAvailabilityService = VolumeActionAvailabilityService;

   constructor(private taskSvc: TaskMonitorService,
         private volumeActionAvailabilityService: VolumeActionAvailabilityService,
   ) {
   }

   actionDisabled = (actionId: string): boolean => {
      return this.loading || !this.getActionStatus(actionId).available;
   };

   getActionButtonTitle = (actionId: string): string => {
      return this.getActionStatus(actionId).message;
   };

   private getActionStatus = (actionId: string): ActionAvailableStatus => {
      return this.volumeActionAvailabilityService.checkAvailability(this.getActionValidationContext(actionId));
   };

   private getActionValidationContext(actionId: string): VolumeActionContext {
      return {
         actionId: actionId,
         selectedVolumes: this.selectedVolumes,
         hasReadPolicyPermission: this.hasReadPolicyPermission,
         hasUpdatePolicyPermission: this.hasUpdatePolicyPermission,
         hasDatastoreFileManagementPermission: this.hasDatastoreFileManagementPermission,
         hasHostStoragePermission: this.hasHostStoragePermission,
         isCnsReconfigPolicySupported: this.isCnsReconfigPolicySupported,
      };
   }

   hasReadUpdatePolicyPermission(): boolean {
      const updatePolicyPermission: boolean = this.isCnsReconfigPolicySupported
            ? this.hasDatastoreFileManagementPermission
            : this.hasUpdatePolicyPermission;
      return this.hasReadPolicyPermission && updatePolicyPermission;
   }

   reapplyPolicy(): void {
      const context: ReapplyPolicyVolumeModalContextData = {
         volumes: this.selectedVolumes,
         isCnsReconfigPolicySupported: this.isCnsReconfigPolicySupported,
      };
      new ModalBuilder(`${VsanRoutes.CNS_VOLUMES_VIEW}/reapplyPolicyVolume`)
            .setSize(VsanDialogUtil.REAPPLY_VOLUME_POLICY_DIALOG.width,
                  VsanDialogUtil.REAPPLY_VOLUME_POLICY_DIALOG.height)
            .open(context)
            .then(async (taskRefs: ManagedObject[]) => await Promise.all(
                  taskRefs.map(taskRef => this.taskSvc.getAwaitResult(taskRef))))
            .then(() => this.onSuccess.emit())
            .catch(e => this.onError.emit(e));
   }

   deleteVolume(): void {
      const context: CnsVolumesDeleteVolumeModalContext = {
         objectRef: ManagedObject.contextObject,
         volume: this.selectedVolumes[0],
      };
      new ModalBuilder(`${VsanRoutes.CNS_VOLUMES_VIEW}/deleteVolume`)
            .setSize(VsanDialogUtil.DELETE_VOLUME_DIALOG.width, VsanDialogUtil.DELETE_VOLUME_DIALOG.height)
            .open(context)
            .then(async taskRef => await this.taskSvc.getAwaitResult(taskRef))
            .then(() => this.onSuccess.emit())
            .catch(e => this.onError.emit(e));
   }

   migrateVolume(): void {
      const context: CnsVolumesMigrateVolumeModalContext = {
         volume: this.selectedVolumes[0],
         storagePolicyName: this.storagePolicyName,
      };

      new ModalBuilder(`${VsanRoutes.CNS_VOLUMES_VIEW}/migrateVolume`)
            .setSize(VsanDialogUtil.MIGRATE_VOLUME_DIALOG.width, VsanDialogUtil.MIGRATE_VOLUME_DIALOG.height)
            .open(context)
            .then(async taskRef => await this.taskSvc.getAwaitResult(taskRef))
            .then(() => this.onSuccess.emit())
            .catch(e => this.onError.emit(e));
   }
}
