import {GlobalRefreshService} from "@service/global-refresh.service";
import {IdGenerator} from "@util/id-generator";
import {PromiseBaton} from "@util/promise/promise-baton";

/**
 * This decorator solve the race condition problem.
 * - Problem - Call single async method multiple times and we can't be sure which result will come last.
 * - Solution - It will resolve/reject only the last promise. On global refresh we cancel the ongoing promise.
 */
export function RaceConditionSafe(): MethodDecorator {
   return function (targetClass: any, functionName: string, descriptor: PropertyDescriptor): void {
      // Prototype level
      const id: string = IdGenerator.getUniqueId();
      const func: (...args: any[]) => Promise<any> = descriptor.value;
      const promiseBaton: PromiseBaton<any> = new PromiseBaton();

      // Instance level
      descriptor.value = function (...args: any[]): Promise<any> {
         addEventListeners(id, promiseBaton);
         return promiseBaton.resolveOnly.call(promiseBaton, func.apply(this, args));
      };
   };

   function addEventListeners(id: string, promiseBaton: PromiseBaton<any>): void {
      GlobalRefreshService.Instance.registerRefreshHandler(id, () => promiseBaton.cancelLastOne(), null, false);
   }
}
