import {Injectable} from "@angular/core";
import {ClrDatagridPagination} from "@clr/angular";
import {DatastoreType} from "@generated/datastore-type";
import {Icon} from "@util/icon";
import {VsanUiUtils} from "@util/vsan-util";

/**
 * Helper service for Migrate volume dialog
 */
@Injectable()
export class MigrateVolumeService {
   getCompatibilityIconClass(isCompatible: boolean): string {
      if (VsanUiUtils.isUnset(isCompatible)) {
         return "";
      }

      return isCompatible
            ? Icon.CLR_SUCCESS_ICON_CLASS
            : Icon.CLR_WARN_ICON_CLASS;
   }

   getCompatibilityIconShape(isCompatible: boolean): string {
      if (VsanUiUtils.isUnset(isCompatible)) {
         return "";
      }

      return isCompatible
            ? Icon.CLR_SUCCESS_STANDARD_ICON_SHAPE
            : Icon.CLR_WARN_STANDARD_ICON_SHAPE;
   }

   getCompatibilityLabel(isCompatible: boolean): string {
      if (VsanUiUtils.isUnset(isCompatible)) {
         return VsanUiUtils.getString("vsan.na.label");
      }

      return isCompatible
            ? VsanUiUtils.getString("cns.volumes.container.volume.migrate.dialog.table.column.compatibility.compatible")
            : VsanUiUtils.getString(
                  "cns.volumes.container.volume.migrate.dialog.table.column.compatibility.incompatible");
   }

   getPaginationLabel(pagination: ClrDatagridPagination, itemsPerPage: number): string {
      return VsanUiUtils.getDatagridPaginatedFooterLabel(pagination.totalItems, pagination.firstItem,
            pagination.lastItem, itemsPerPage,
            VsanUiUtils.getString("cns.volumes.container.volume.migrate.dialog.table.datastore"),
            VsanUiUtils.getString("cns.volumes.container.volume.migrate.dialog.table.datastores"),
            VsanUiUtils.getString("cns.volumes.container.volume.migrate.dialog.table.page"));
   }

   getDatastoreTypeLabel(type: DatastoreType): string {
      switch (type) {
         case DatastoreType.VSAN:
            return VsanUiUtils.getString("cns.volumes.container.volume.migrate.dialog.table.column.type.vsan");
         case DatastoreType.VSAN_DIRECT:
            return VsanUiUtils.getString("cns.volumes.container.volume.migrate.dialog.table.column.type.vsand");
         case DatastoreType.PMEM:
            return VsanUiUtils.getString("cns.volumes.container.volume.migrate.dialog.table.column.type.pmem");
         case DatastoreType.VMFS:
            return VsanUiUtils.getString("cns.volumes.container.volume.migrate.dialog.table.column.type.vmfs");
         case DatastoreType.NFS:
            return VsanUiUtils.getString("cns.volumes.container.volume.migrate.dialog.table.column.type.nfs3");
         case DatastoreType.NFS41:
            return VsanUiUtils.getString("cns.volumes.container.volume.migrate.dialog.table.column.type.nfs41");
         case DatastoreType.VVOL:
            return VsanUiUtils.getString("cns.volumes.container.volume.migrate.dialog.table.column.type.vvol");
         default:
            return VsanUiUtils.getString("vsan.na.label");
      }
   }
}