import {Component, Input} from "@angular/core";
import {ModalBuilder} from "@util/modal-builder";
import {Icon} from "@util/icon";
import {Volume} from "@generated/volume";
import {CnsHealthStatus} from "@generated/cns-health-status";
import {CnsVolumeType} from "@generated/cns-volume-type";
import {VolumesUtil} from "@util/volumes-util";

@Component({
   templateUrl: "cns-health-status-cell.html",
   styleUrls: ["cns-health-status-cell.scss"],
   selector: "cns-health-status-cell",
})

/**
 * The component is used to show the health status of a CNS volume:
 * red- unhealthy/not accessible
 * green - healthy/accessible
 * yellow - repairing/partially accessible
 * unknown
 * The component is used in the grid and grid details.
 */
export class CnsHealthStatusCell {

   @Input()
   volume: Volume;

   Icon = Icon;
   private static ACCESSIBILITY_DIALOG_WIDTH: number = 740;
   private static ACCESSIBILITY_DIALOG_HEIGHT: number = 360;

   /**
    * The label for accessibility status must become a link when accessibility status is
    * notAccessible or partiallyAccessible in the other cases when it is null or accessible, or for file volumes
    * the label must not be a link.
    * File volumes are located on vsan datastore and are always accessible, so volume accessibility
    * doesn't make sense for them
    * @param volume - cns volume.
    */
   isLink = (volume: Volume): boolean => {
      if (VolumesUtil.isFileVolume(volume)) {
         return false;
      }

      switch (volume.healthStatus) {
         case CnsHealthStatus.red:
         case CnsHealthStatus.yellow:
            return true;
         case CnsHealthStatus.green:
         case CnsHealthStatus.unknown:
         default:
            return false;
      }
   };

   openAccessibilityDialog(volume: Volume) {
      new ModalBuilder("cnsVolumes/accessibility")
            .setDimensions([
                  CnsHealthStatusCell.ACCESSIBILITY_DIALOG_WIDTH, CnsHealthStatusCell.ACCESSIBILITY_DIALOG_HEIGHT,
            ])
            .open({volume: volume});
   }
}