import {Pipe, PipeTransform} from "@angular/core";
import {Icon} from "@util/icon";
import {CnsHealthStatus} from "@generated/cns-health-status";

/**
 * This pipe changes icon shapes according the CnsHealthStatus value.
 */
@Pipe({
   name: "cnsHealthStatusIconShape",
})
export class HealthStatusIconShapePipe implements PipeTransform {
   transform(status: CnsHealthStatus): string {
      if (!status) {
         return "";
      }

      switch (status) {
         case CnsHealthStatus.green:
            return Icon.CLR_SUCCESS_STANDARD_ICON_SHAPE;
         case CnsHealthStatus.red:
            return Icon.CLR_MINUS_CIRCLE_ICON_SHAPE;
         case CnsHealthStatus.yellow:
            return Icon.CLR_ERROR_STANDARD_ICON_SHAPE;
         case CnsHealthStatus.unknown:
            return Icon.CLR_HELP;
         default:
            return Icon.CLR_ERROR_STANDARD_ICON_SHAPE;
      }
   }
}