import {Component, OnInit} from "@angular/core";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {CnsVolumesDeleteVolumeModalContext} from "./delete-volume-modal-context.data";
import {CnsService} from "@generated/cns-service";
import {Volume} from "@generated/volume";
import {VolumesVmService} from "../../volumes-vm-service";

@Component({
   templateUrl: "delete-volume.html",
   selector: "vsan-volumes-delete-volume",
})
export class DeleteVolumeComponent implements OnInit {
   objectRef: ManagedObject;
   volume: Volume;
   errors: string[];
   loading: boolean;

   constructor(private cnsService: CnsService,
         private volumesVmService: VolumesVmService) {
   }

   ngOnInit(): void {
      const context: CnsVolumesDeleteVolumeModalContext =
            <CnsVolumesDeleteVolumeModalContext>VsanUiUtils.getModalContext();
      this.objectRef = context.objectRef;
      this.volume = context.volume;
      this.loading = true;
      this.volumesVmService.getVMData(this.volume).then(() => {
         if (this.isVolumeAttached) {
            this.errors = [VsanUiUtils.getString("cns.volumes.container.volume.delete.dialog.validation.vm.attached",
                           this.volume.vmData[0].name)];
         }
      }).catch(error => this.errors = [error])
        .finally(() => this.loading = false);
   }

   async confirm() {
      this.errors = null;
      try {
         this.loading = true;
         const taskRef: ManagedObject = await this.cnsService.deleteVolume(this.objectRef, this.volume.id);
         VsanUiUtils.closeModalDialog(taskRef);
      } catch (error) {
         this.errors = [error];
      } finally {
         this.loading = false;
      }
   }

   get isVolumeAttached(): boolean {
      return this.volume && !VsanUiUtils.isEmpty(this.volume.vmData);
   }

   get hasErrors(): boolean {
      return !VsanUiUtils.isEmpty(this.errors);
   }
}