import {Pipe, PipeTransform} from "@angular/core";
import {VsanUiUtils} from "@util/vsan-util";
import {CnsVolumeType} from "@generated/cns-volume-type";

/**
 * This pipe returns the label for a volume type.
 */
@Pipe({
   name: "cnsVolumeTypeLabel",
})
export class VolumeTypeLabelPipe implements PipeTransform {
   transform(type: CnsVolumeType): string {
      switch (type) {
         case CnsVolumeType.FILE:
            return VsanUiUtils.getString("cns.volumes.container.volumeType.file");
         case CnsVolumeType.BLOCK:
         default:
            return VsanUiUtils.getString("cns.volumes.container.volumeType.block");
      }
   }
}