import {Component, OnInit} from "@angular/core";
import {ClrDatagridPagination} from "@clr/angular";
import {CnsService} from "@generated/cns-service";
import {DatastoreType} from "@generated/datastore-type";
import {DestinationDatastore} from "@generated/destination-datastore";
import {Volume} from "@generated/volume";
import {VolumeMigrateData} from "@generated/volume-migrate-data";
import {NavigationService} from "@navigation/navigation.service";
import {ErrorMessage} from "@service/flow/error-message.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {CnsVolumesMigrateVolumeModalContext} from "./migrate-volume-modal-context.data";
import {MigrateVolumeService} from "./migrate-volume.service";

@Component({
   templateUrl: "migrate-volume.html",
   selector: "vsan-volumes-migrate-volume",
   styleUrls: ["migrate-volume.scss"],
})
export class MigrateVolumeComponent implements OnInit {
   readonly DEFAULT_ITEMS_PER_PAGE: number = 10;

   volume: Volume;
   storagePolicyName: string;
   migrateVolume: VolumeMigrateData;

   selectedDatastore: DestinationDatastore;

   @ErrorMessage()
   errorMessage: string;

   @LoadingIndicator()
   loading: boolean;

   warning: string;
   showKBLink: boolean;

   documentationUrl: string;
   // TODO: set a valid GUID
   private readonly MIGRATE_VOLUME_DOCUMENTAION_GUID: string = "";

   constructor(private cnsService: CnsService,
         private migrationService: MigrateVolumeService,
         private navigationService: NavigationService) {
   }

   async ngOnInit(): Promise<void> {
      this.documentationUrl = this.navigationService.getVmwareHelpUrl(NavigationService.NETWORKING_URL,
            this.MIGRATE_VOLUME_DOCUMENTAION_GUID);

      const context: CnsVolumesMigrateVolumeModalContext = VsanUiUtils.getModalContext();
      this.volume = context.volume;
      this.storagePolicyName = context.storagePolicyName;
      await this.getMigrateVolumeData(this.volume);
   }

   @Loader()
   async getMigrateVolumeData(volume: Volume): Promise<void> {
      this.migrateVolume = await this.cnsService.getMigrateVolumeData(volume);

      if (!VsanUiUtils.isEmpty(this.migrateVolume.destinationDatastores)) {
         this.setInitialSelection();
      }
   }

   @Loader()
   async confirm(): Promise<void> {
      // TODO: integrate with backend
      const taskRef: ManagedObject = new ManagedObject();
      VsanUiUtils.closeModalDialog(taskRef);
   }

   get canMigrate(): boolean {
      return !this.loading && !this.errorMessage
            && this.migrateVolume && !VsanUiUtils.isEmpty(this.migrateVolume.destinationDatastores);
   }

   getPaginationLabel(pagination: ClrDatagridPagination): string {
      return this.migrationService.getPaginationLabel(pagination, this.DEFAULT_ITEMS_PER_PAGE);
   }

   getDatastoreTypeLabel(type: DatastoreType): string {
      return this.migrationService.getDatastoreTypeLabel(type);
   }

   private setInitialSelection(): void {
      // Set a warning when the storage policy is not retrieved but there are accessible datastores
      if (!this.storagePolicyName) {
         this.warning = VsanUiUtils.getString("cns.volumes.container.volume.migrate.dialog.warning.noStoragePolicy");
      }

      this.selectionChanged(this.migrateVolume.destinationDatastores[0]);
   }

   selectionChanged = (target: DestinationDatastore): void => {
      this.selectedDatastore = target;
      if (!this.storagePolicyName) {
         return;
      }

      this.showKBLink = target.policyCompliant === false;
      this.warning = this.showKBLink
            ? VsanUiUtils.getString("cns.volumes.container.volume.migrate.dialog.warning.notCompliantDatastore",
                  this.storagePolicyName)
            : "";
   };

   getCompatibilityIconClass(isCompatible: boolean): string {
      return this.migrationService.getCompatibilityIconClass(isCompatible);
   }

   getCompatibilityIconShape(isCompatible: boolean): string {
      return this.migrationService.getCompatibilityIconShape(isCompatible);
   }

   getCompatibilityLabel(isCompatible: boolean): string {
      return this.migrationService.getCompatibilityLabel(isCompatible);
   }
}