import {Component, Input} from "@angular/core";
import {ModalBuilder} from "@util/modal-builder";
import {Icon} from "@util/icon";
import {Volume} from "@generated/volume";
import {StorageCompliance} from "@generated/storage-compliance";

@Component({
   templateUrl: "cns-compliance-status-cell.html",
   styleUrls: ["cns-compliance-status-cell.scss"],
   selector: "cns-compliance-status-cell",
})
/**
 * The component is used to show the compliance status of a CNS volume:
 * outOfDate
 * compliant
 * nonCompliant
 * notApplicable
 * unknown
 * The component is used in the grid and grid details.
 */
export class CnsComplianceStatusCell {

   @Input()
   volume: Volume;

   Icon = Icon;
   StorageCompliance = StorageCompliance;

   private static COMPLIANCE_FAILURE_DIALOG_WIDTH: number = 800;
   private static COMPLIANCE_FAILURE_DIALOG_HEIGHT: number = 364;

   openComplianceFailureDialog(volume: Volume) {
      new ModalBuilder("cnsVolumes/complianceFailures")
            .setDimensions([
               CnsComplianceStatusCell.COMPLIANCE_FAILURE_DIALOG_WIDTH,
               CnsComplianceStatusCell.COMPLIANCE_FAILURE_DIALOG_HEIGHT,
            ])
            .open({volume: volume});
   }
}
