import {Component, EventEmitter} from "@angular/core";
import {Subject} from "rxjs";
import {auditTime} from "rxjs/operators";
import {ManagedObject} from "@service/managed-object";
import {Logger} from "@util/logger";
import {VsanUiUtils} from "@util/vsan-util";
import {CnsService} from "@generated/cns-service";
import {LabelData} from "@generated/label-data";
import {CustomFilter} from "@component/cross-column-filter/custom-filter";

/**
 * This class is used to search for labels in CNS view by key:value pair.
 */
@Component({
   templateUrl: "label-filter.html",
   styleUrls: ["label-filter.scss"],
   selector: "cns-label-filter",
})
export class LabelFilterComponent implements CustomFilter {
   private static REFRESH_WAIT_MS: number = 2000; // 2 sec between query requests.
   loading: boolean = false;
   alertMessage: string;
   selectedLabels: LabelData[] = [];
   queryFilterResults: any[];
   labelSearchValue: string = null;
   hasMore: boolean;
   refresh: Subject<string> = new Subject<string>();
   onFilterSet = new EventEmitter<LabelData[]>();

   constructor(private cnsService: CnsService) {
      // Make sure we do not do requests on each keyup.
      this.refresh.pipe(auditTime(LabelFilterComponent.REFRESH_WAIT_MS)).subscribe(this.dispatchQueryVolumesRequest);

      // TODO: Update CustomFilter with onFilterCleared method:
      // this.filterService.onFilterCleared.subscribe(() => {
      //    this.labelSearchValue = null;
      //    this.clearFilterResults();
      // });
   }

   setFilterToComponent(filterValue: Array<{key: string, value: string}>) {
      this.selectedLabels = !VsanUiUtils.isEmpty(filterValue) ? filterValue : [];
   }

   onSearchChange(value: string) {
      this.refresh.next(value);
   }

   isLabelSelected(label: LabelData): boolean {
      return this.findLabelIndex(label) > -1;
   }

   private findLabelIndex(label: LabelData): number {
      return this.selectedLabels.findIndex((item) => item.key === label.key && item.value === label.value);
   }

   selectLabel(label: LabelData) {
      const selectedLabelIndex: number = this.findLabelIndex(label);
      if (selectedLabelIndex === -1) {
         this.selectedLabels.push(label);
      } else {
         this.selectedLabels.splice(selectedLabelIndex, 1);
      }
      // TODO: Change the type of the filter state
      this.onFilterSet.emit(this.selectedLabels);
   }

   dispatchQueryVolumesRequest = async (searchString: string) => {
      this.loading = true;
      this.alertMessage = null;
      const components: string[] = searchString.split(":", 2);
      const key: string = components[0];
      const value: string = components[1];
      if (key) {
         try {
            const result = await this.cnsService.queryLabels(ManagedObject.contextObject, key, value);
            this.queryFilterResults = result.labels;
            this.hasMore = result.hasMore;
         } catch (e) {
            Logger.error(e);
            this.alertMessage = e;
            this.clearFilterResults();
         }
      } else {
         this.clearFilterResults();
      }
      this.loading = false;
   };

   private clearFilterResults() {
      this.queryFilterResults = [];
      this.hasMore = false; // Reset has more, as we do not know if there are any results at all.
   }
}