import {Component, Input} from "@angular/core";
import {BasicVmData} from "@generated/basic-vm-data";
import {VsanUiUtils} from "@util/vsan-util";

@Component({
   templateUrl: "volumes-view-details-pod-row.html",
   styleUrls: ["volumes-view-details-pod-row.scss"],
   selector: "cns-volumes-view-details-pod-row",
})

/**
 * Represents a single pod row (label and value) from grid details view
 */

export class VolumesViewDetailsPodRowComponent {

   POD_DELIMITER: string = VsanUiUtils.getString("vsan.common.comma");

   @Input()
   vmData: BasicVmData[];

   @Input()
   vmDataLoading: boolean;

   @Input()
   podNames: string[];

   // TODO: (vasilevd) Works for block volumes only. Should be handled for file volumes as well.
   showPodVMCell(): boolean {
      return this.podNames && this.podNames.length === 1 &&
            this.vmData && this.vmData.length === 1 &&
            this.vmData[0].isPodVM &&
            this.podNames[0] === this.vmData[0].name;
   }
}
