import {Volume} from "@generated/volume";
import {VsanUiUtils} from "@util/vsan-util";
import {CnsVolumeType} from "@generated/cns-volume-type";

export class VolumesUtil {
   static isFileVolume(volumeItem: Volume): boolean {
      return volumeItem && (volumeItem.type === CnsVolumeType.FILE);
   }

   static isBlockVolume(volumeItem: Volume): boolean {
      return volumeItem && (volumeItem.type === CnsVolumeType.BLOCK);
   }

   static isAttachedContainerVolume(volumeItem: Volume): boolean {
      return volumeItem && !VsanUiUtils.isEmpty(volumeItem.vmData);
   }

   static isAssociatedVmAccessible(volumeItem: Volume): boolean {
      return VolumesUtil.isAttachedContainerVolume(volumeItem) && volumeItem.vmData[0].hasReadPermissions;
   }
}