import {Component, OnInit} from "@angular/core";
import {CnsService} from "@generated/cns-service";
import {Volume} from "@generated/volume";
import {ErrorHandler} from "@service/flow/error-handler.decorator";
import {Loader} from "@service/flow/loader.decorator";
import {LoadingIndicator} from "@service/flow/loading-indicator.decorator";
import {ManagedObject} from "@service/managed-object";
import {VsanUiUtils} from "@util/vsan-util";
import {VolumeActionAvailabilityService} from "../volume-action-availability.service";
import {ReapplyPolicyVolumeModalContextData} from "./reapply-policy-volume-modal-context.data";

@Component({
   templateUrl: "reapply-policy-volume.html",
   selector: "vsan-reapply-policy-volume",
   styleUrls: ["reapply-policy-volume.scss"],
})
export class ReapplyPolicyVolumeComponent implements OnInit {
   volumes: Volume[] = [];
   eligibleVolumes: Volume[] = [];
   isCnsReconfigPolicySupported: boolean;
   description1: string = "";
   description2: string = "";
   errors: string[];

   @LoadingIndicator()
   loading: boolean;

   constructor(private cnsService: CnsService,
         private volumeActionAvailabilityService: VolumeActionAvailabilityService) {
   }

   ngOnInit(): void {
      const context: ReapplyPolicyVolumeModalContextData = VsanUiUtils.getModalContext();
      this.volumes = context.volumes;
      this.isCnsReconfigPolicySupported = context.isCnsReconfigPolicySupported;
      this.eligibleVolumes = this.volumeActionAvailabilityService.getReapplyPolicyEligibleVolumes(
            this.isCnsReconfigPolicySupported, this.volumes);
      this.setReapplyStorageDescriptions();
   }

   private setReapplyStorageDescriptions(): void {
      const key: string = this.isCnsReconfigPolicySupported
            ? "cns.volumes.container.volume.reapplyPolicy.dialog.text1.new"
            : "cns.volumes.container.volume.reapplyPolicy.dialog.text1";
      this.description1 = VsanUiUtils.getString(key);
      this.description2 = VsanUiUtils.getString("cns.volumes.container.volume.reapplyPolicy.dialog.text2",
            this.eligibleVolumes.length, this.volumes.length);
   }

   @Loader()
   async confirm(): Promise<void> {
      this.errors = null;
      const result: ManagedObject[] = await this.cnsService.reapplyStoragePolicy(
            ManagedObject.contextObject, this.eligibleVolumes, this.isCnsReconfigPolicySupported);
      VsanUiUtils.closeModalDialog(result);
   }

   @ErrorHandler()
   private onError(): void {
      this.errors = [VsanUiUtils.getString("cns.volumes.container.reapply.error")];
   }
}